import { updateColors, updateColorFromImage } from "material-color-css";

let scheme;
let colorFromStorage;

chrome.storage.local.get(["readeaser_colorFromStorage"]).then((res) => {
  colorFromStorage = res["readeaser_colorFromStorage"] || "#769CDF";
  if (scheme) {
    updateColors(colorFromStorage, scheme);
  } else {
    updateColors(colorFromStorage);
  }
});

const color = document.querySelector('input[type="color"]');
color.addEventListener("input", (e) => {
  const color = e.target.value;
  chrome.storage.local.set({ readeaser_colorFromStorage: color });
  if (scheme) {
    updateColors(color, scheme);
  } else {
    updateColors(color);
  }
});

