function eventMap(form1, form2, name1, name2) {
  const darkColorForm = document.querySelector(`${form1}`);
  const darkButtonForm = document.querySelector(`${form2}`);

  darkColorForm.addEventListener("change", (event) => {
    if (event.target.name === `${name1}`) {
      const mapValue = event.target.getAttribute("value");
      if (mapValue) {
        const correspondingRadio = darkButtonForm.querySelector(
          `input[data-map="${mapValue}"]`
        );
        if (correspondingRadio) {
          correspondingRadio.click();
        }
      }
    }
  });

  darkButtonForm.addEventListener("change", (event) => {
    if (event.target.name === `${name2}`) {
      const mapValue = event.target.getAttribute("value");

      if (mapValue) {
        const correspondingRadio = darkColorForm.querySelector(
          `input[data-map="${mapValue}"]`
        );
        if (correspondingRadio) {
          correspondingRadio.click();
        }
      }
    }
  });
}

let db;

window.onload = function () {
  initDB();

  document
    .querySelector("#fontUpload")
    .addEventListener(
      "input",
      createHandleFileUpload(
        "fonts",
        ".fontList .options",
        ".fontList .option",
        ".fontSelect",
        applyFont
      )
    );

  document.querySelectorAll(".fontList .option").forEach((font) => {
    font.addEventListener("click", applyFont);
  });

  document
    .querySelector("#imageInput")
    .addEventListener(
      "input",
      createHandleFileUpload(
        "image",
        ".imageList .options",
        ".imageList .option",
        ".imageSelect",
        applyImage
      )
    );

  document.querySelectorAll(".imageList .option").forEach((image) => {
    image.addEventListener("click", applyImage);
  });

  document
    .querySelector(".theme_save")
    .addEventListener(
      "click",
      createHandleDataUpload(
        "theme",
        ".themeList .options",
        ".themeList .option",
        ".themeSelect",
        applyTheme
      )
    );
};

function initDB() {
  const request = indexedDB.open("readeaserDB", 1);

  request.onerror = function (event) {
    console.log("Error opening IndexedDB:", event);
  };

  request.onsuccess = function (event) {
    db = event.target.result;
    loadStorage(
      "fonts",
      ".fontList .options",
      ".fontList .option",
      ".fontSelect",
      applyFont
    );
    loadStorage(
      "image",
      ".imageList .options",
      ".imageList .option",
      ".imageSelect",
      applyImage
    );
    loadStorage(
      "theme",
      ".themeList .options",
      ".themeList .option",
      ".themeSelect",
      applyTheme
    );
  };

  request.onupgradeneeded = function (event) {
    db = event.target.result;
    if (!db.objectStoreNames.contains("fonts")) {
      db.createObjectStore("fonts", { keyPath: "name" });
    }

    if (!db.objectStoreNames.contains("image")) {
      db.createObjectStore("image", { keyPath: "name" });
    }
    if (!db.objectStoreNames.contains("theme")) {
      db.createObjectStore("theme", { keyPath: "name" });
    }
  };
}

function createHandleFileUpload(
  fileType,
  listsName,
  listName,
  select,
  applyFunction
) {
  return function (e) {
    handleFileUpload(e, fileType, listsName, listName, select, applyFunction);
  };
}

function createHandleDataUpload(
  fileType,
  listsName,
  listName,
  select,
  applyFunction
) {
  return function (e) {
    handleDataUpload(e, fileType, listsName, listName, select, applyFunction);
  };
}

function createRemoveFromList(fileType) {
  return function (e) {
    // e.stopPropagation();
    removeFromList(e, fileType);
  };
}

function handleFileUpload(
  e,
  fileType,
  listsName,
  listName,
  select,
  applyFunction
) {
  // file = file || e.target.files[0];
  const file = e.target.files[0];
  handleUpload(e, file, fileType, listsName, listName, select, applyFunction);
}

function handleDataUpload(
  e,
  fileType,
  listsName,
  listName,
  select,
  applyFunction
) {
  const board = document.querySelector(".textboard");
  const back = document.querySelector(".text_content_back");
  const text = document.querySelector(".ch");
  const dark = document.querySelector("input[name='mode']:checked");

  let isdark = false;
  if (dark.value === "dark") isdark = true;

  let bcolor = window.getComputedStyle(board).backgroundColor;
  let fcolor = window.getComputedStyle(back).backgroundColor;
  let fontFamily = window.getComputedStyle(text).fontFamily;
  let fontWeight = window.getComputedStyle(text).fontWeight;
  let backgroundImage = window.getComputedStyle(back).backgroundImage;
  let backgroundRepeat = window.getComputedStyle(back).backgroundRepeat;
  let backgroundSize = window.getComputedStyle(back).backgroundSize;
  let filter = window.getComputedStyle(back).filter;

  imageUrl();

  async function imageUrl() {
    console.log(backgroundImage);
    if (backgroundImage && backgroundImage != "none") {
      let image = await applyBlurToImageAndSave(backgroundImage, filter);
      let data = {
        isdark: isdark,
        bcolor: bcolor,
        fcolor: fcolor,
        fontFamily: fontFamily,
        fontWeight: fontWeight,
        backgroundImage: backgroundImage,
        backgroundRepeat: backgroundRepeat,
        backgroundSize: backgroundSize,
        filter: filter,
        image: image,
      };
      data = JSON.stringify(data);
      const blob = new Blob([data], { type: "application/json" });

      // console.log(blob instanceof Blob);
      console.log("image:", image);

      handleUpload(
        e,
        blob,
        fileType,
        listsName,
        listName,
        select,
        applyFunction
      );
    } else {
      let data = {
        isdark: isdark,
        bcolor: bcolor,
        fcolor: fcolor,
        fontFamily: fontFamily,
        fontWeight: fontWeight,
        backgroundImage: backgroundImage,
        backgroundRepeat: backgroundRepeat,
        backgroundSize: backgroundSize,
        filter: filter,
        // image: image,
      };
      data = JSON.stringify(data);
      const blob = new Blob([data], { type: "application/json" });

      // console.log(blob instanceof Blob);
      // console.log("image:", image);

      handleUpload(
        e,
        blob,
        fileType,
        listsName,
        listName,
        select,
        applyFunction
      );
    }
  }
}

async function applyBlurToImageAndSave(backgroundImage, filter) {
  const backgroundImageURL = backgroundImage.slice(5, -2);

  const img = new Image();
  img.crossOrigin = "Anonymous";

  const imageLoadPromise = new Promise((resolve, reject) => {
    img.onload = () => resolve(img);
    img.onerror = () => reject(new Error("图像加载失败，请检查URL是否正确。"));
  });

  img.src = backgroundImageURL;

  try {
    await imageLoadPromise;

    const canvas = document.createElement("canvas");
    const ctx = canvas.getContext("2d");

    canvas.width = img.width;
    canvas.height = img.height;

    ctx.drawImage(img, 0, 0);

    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.filter = filter;

    ctx.drawImage(img, 0, 0);

    const dataURL = canvas.toDataURL("image/webp");
    console.log("dataUrl", dataURL);

    return dataURL;
  } catch (error) {
    console.error(error.message);
    throw error;
  }
}

function handleUpload(
  e,
  file,
  fileType,
  listsName,
  listName,
  select,
  applyFunction
) {
  // file = file || e.target.files[0];
  const customName = prompt("请输入名称:", file.name);
  if (!customName) {
    alert("名称不能为空");
    e.target.value = "";
    return;
  }
  const reader = new FileReader();

  reader.onload = function (event) {
    const item = {
      name: customName,
      data: event.target.result,
    };

    const transaction = db.transaction([fileType], "readwrite");
    const objectStore = transaction.objectStore(fileType);
    const request = objectStore.add(item);

    request.onsuccess = function () {
      console.log(`Added ${fileType} to IndexedDB:`, item);
      addToList(customName, listsName, listName, select, applyFunction);
    };

    request.onerror = function () {
      console.log(`Error adding ${fileType} to IndexedDB:`, request.error);
    };
  };

  reader.readAsDataURL(file);
}

function deleteFile(fileType, fileName) {
  const transaction = db.transaction([fileType], "readwrite");
  const objectStore = transaction.objectStore(fileType);
  const request = objectStore.delete(fileName);
  request.onsuccess = () => {
    console.log(`文件 "${fileName}" 已从 IndexedDB 中删除。`);
  };

  request.onerror = (event) => {
    console.error("删除文件时出错:", event.target.error);
  };
}

function removeFromList(e, fileType) {
  console.log("try to delete");
  const option = e.target.parentElement;
  const fileName = option.querySelector("div").textContent;
  console.log(option);
  console.log(fileName);
  const isConfirmed = confirm(`确定删除 "${fileName}" ？`);

  if (isConfirmed) {
    option.remove();
    deleteFile(fileType, fileName);
  }
}

function addToList(fileName, listsName, listName, select, applyFunction) {
  const list = document.querySelector(listsName);
  const option = document.createElement("div");
  option.className = "option";
  option.innerHTML = `
    <div>${fileName}</div>
    <svg xmlns="http://www.w3.org/2000/svg" height="24px" viewBox="0 -960 960 960" width="24px" fill="#5f6368">
      <path d="m256-200-56-56 224-224-224-224 56-56 224 224 224-224 56 56-224 224 224 224-56 56-224-224-224 224Z"/>
    </svg>`;
  list.appendChild(option);
  selected(listName, select);

  document.querySelectorAll(listName).forEach((item) => {
    item.addEventListener("click", applyFunction);
  });
}

function loadStorage(fileType, listsName, listName, select, applyFunction) {
  const transaction = db.transaction([fileType], "readonly");
  const objectStore = transaction.objectStore(fileType);

  objectStore.openCursor().onsuccess = function (event) {
    const cursor = event.target.result;
    if (cursor) {
      addToList(cursor.value.name, listsName, listName, select, applyFunction);
      cursor.continue();
    }
  };
}

function applyFont(event) {
  if (event.target.tagName === "DIV") {
    applyFonts(event);
  } else {
    createRemoveFromList("fonts")(event);
  }
}

function applyFonts(event) {
  console.log("apply font");
  const text = event.target.textContent;
  const selectedFont = text.replace(/\s+/g, "");

  if (selectedFont === "默认") {
    document.querySelector(".ch").style.fontFamily = "";
    document.querySelector(".en").style.fontFamily = "";
  } else {
    const transaction = db.transaction(["fonts"], "readonly");
    const objectStore = transaction.objectStore("fonts");
    const request = objectStore.get(selectedFont);

    request.onsuccess = function (event) {
      const font = event.target.result;

      if (font && font.name && font.data) {
        console.log("Font found in IndexedDB: ", font);

        const newFontFace = new FontFace(font.name, `url(${font.data})`);

        newFontFace
          .load()
          .then(function (loadedFont) {
            if (!document.fonts.has(loadedFont)) {
              document.fonts.add(loadedFont);
            }
            document.querySelector(".ch").style.fontFamily = font.name;
            document.querySelector(".en").style.fontFamily = font.name;
          })
          .catch(function (error) {
            console.error("Error loading font:", error);
          });
      } else {
        console.log("Font not found: ", selectedFont);
      }
    };

    request.onerror = function (event) {
      console.error("Error fetching font from IndexedDB: ", event);
    };
  }
}

function applyImage(event) {
  if (event.target.tagName === "DIV") {
    applyImg(event);
  } else {
    createRemoveFromList("image")(event);
  }
}

function applyImg(event) {
  console.log("apply image");
  const text = event.target.textContent;
  const selected = text.replace(/\s+/g, "");

  if (selected === "默认") {
    document.querySelector(".text_content_back").style.background = "";
  } else {
    const transaction = db.transaction(["image"], "readonly");
    const objectStore = transaction.objectStore("image");
    const request = objectStore.get(selected);

    request.onsuccess = function (event) {
      const item = event.target.result;

      if (item && item.data) {
        console.log("Image found in IndexedDB: ", item);

        // const blob = new Blob([item.data]);
        // let imageUrl = URL.createObjectURL(blob);
        document.querySelector(
          ".text_content_back"
        ).style.backgroundImage = `url(${item.data})`;
      }
    };

    request.onerror = function (event) {
      console.error("Error fetching image from IndexedDB: ", event);
    };
  }
}

function applyTheme(event) {
  if (event.target.tagName === "DIV") {
    applyThemes(event);
  } else {
    createRemoveFromList("theme")(event);
  }
}

function applyThemes(event) {
  console.log("apply theme");
  const text = event.target.textContent;
  const selected = text.replace(/\s+/g, "");

  if (selected === "默认") {
    console.log("默认主题");
    // localtion.reload();
    let theme_none = "";
    chrome.storage.local.set({ readeaser_theme: theme_none });
  } else {
    const transaction = db.transaction(["theme"], "readonly");
    const objectStore = transaction.objectStore("theme");
    const request = objectStore.get(selected);

    request.onsuccess = function (event) {
      const item = event.target.result;

      if (item && item.data) {
        console.log("Theme found in IndexedDB: ", item);
        const blob = base64ToBlob(item.data, "application/json");
        readBlobAsJson(blob)
          .then((data) => {
            console.log("解析后的数据: ", data);
            chrome.storage.local.set({ readeaser_theme: selected });
            applyData(data);
          })
          .catch((error) => {
            console.error("解析数据时发生错误:", error);
          });
      } else {
        console.log("没有找到匹配的主题或数据类型不符");
      }

      request.onerror = function (event) {
        console.error("Error fetching image from IndexedDB: ", event);
      };
    };
  }
}

function applyData(data) {
  const board = document.querySelector(".textboard");
  const back = document.querySelector(".text_content_back");
  const text = document.querySelector(".ch");

  try {
    if (!board) throw new Error("找不到类名为 'textboard' 的元素");
    if (!back) throw new Error("找不到类名为 'text_content_back' 的元素");
    if (!text) throw new Error("找不到类名为 'ch' 的元素");

    board.style.backgroundColor = data.bcolor;
    back.style.backgroundColor = data.fcolor;
    text.style.fontFamily = data.fontFamily;
    text.style.fontWeight = data.fontWeight;
    back.style.backgroundImage = data.backgroundImage;
    back.style.backgroundRepeat = data.backgroundRepeat;
    back.style.backgroundSize = data.backgroundSize;
    back.style.filter = data.filter;

    if (data.isdark) {
      document.querySelector("input[name='mode'][value='dark']").checked = true;
    } else {
      document.querySelector(
        "input[name='mode'][value='light']"
      ).checked = true;
    }
  } catch (error) {
    console.error(error.message);
  }
}

function base64ToBlob(base64, contentType) {
  const byteCharacters = atob(base64.split(",")[1]);
  const byteArrays = [];

  for (let offset = 0; offset < byteCharacters.length; offset += 512) {
    const slice = byteCharacters.slice(offset, offset + 512);
    const byteNumbers = new Array(slice.length);
    for (let i = 0; i < slice.length; i++) {
      byteNumbers[i] = slice.charCodeAt(i);
    }
    const byteArray = new Uint8Array(byteNumbers);
    byteArrays.push(byteArray);
  }

  return new Blob(byteArrays, { type: contentType });
}

function readBlobAsJson(blob) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();

    reader.onload = function (event) {
      try {
        const jsonString = event.target.result;
        const data = JSON.parse(jsonString);
        resolve(data);
      } catch (error) {
        reject(error);
      }
    };

    reader.onerror = function (event) {
      reject(new Error("读取 Blob 文件时发生错误"));
    };

    reader.readAsText(blob);
  });
}

