document.querySelectorAll(".repeat input[name='repeat']").forEach((re) => {
  re.addEventListener("change", (e) => {
    const value = e.target.value;
    const content = document.querySelector(".text_content_back");
    if (value === "repeat") {
      content.style.backgroundRepeat = "repeat";
      content.style.backgroundSize = "";
    } else {
      content.style.backgroundRepeat = "no-repeat";
      content.style.backgroundSize = "cover";
    }
  });
});

